
.block {
    display: inline-block;
    background-color: rgba(13, 6, 0, 0.4);
    border-radius: 2vw;
    text-align: justify;
    text-justify: inter-word;
    padding: 5vh 4vw 2vh 4vw;
    margin: 3vh 2vw 3vh 2vw;
}

.image-container {
   float: left;
   padding: 0 2vw 5vh 0;
}

.image {
    height: 65vh;
    max-width:100vw;
    border-radius: 5%;
}

